<template>
    <div>
        <MainPageHeader/>
        <div class="content">
            <div class="header">
                <h1>Gatunki</h1>
                <SmallRouterLink to="/tab2/add-genre" icon="plus" border-color="primary">Dodaj</SmallRouterLink>
            </div>
            <div class="genre-list">
                <GenreRow
                    v-for="genre in genres"
                    :key="genre.key"
                    :genre="genre"
                    @delete="handleGenreDelete"
                    />
            </div>
        </div>
        <ConfirmDeleteGenrePopup
            :enabled="deletedGenreKey != null"
            :genre="deletedGenre"
            @confirm="handleConfirmGenreDelete"
            @close="deletedGenreKey = null"
            />
        <ContentLoader v-if="!contentLoaded" />
    </div>
</template>

<script>
    import GenreRow from '../components/genreList/GenreRow.vue';
    import MainPageHeader from '../components/MainPageHeader.vue';
    import SmallRouterLink from '../components/input/SmallRouterLink.vue';
    import ContentLoader from '../components/ContentLoader.vue';
    import ConfirmDeleteGenrePopup from '../components/genreList/ConfirmDeleteGenrePopup.vue';
    import { getGenreService } from '../services/genreService';
    import { createCancellable } from '../helpers/asyncHelper';

    export default {
        name: 'Tab2',
        components: {
            MainPageHeader,
            SmallRouterLink,
            GenreRow,
            ContentLoader,
            ConfirmDeleteGenrePopup,
        },
        data() {
            return {
                contentLoaded: false,
                genres: [],
                deletedGenreKey: null,
            };
        },
        computed: {
            deletedGenre() {
                return this.genres.find(g => g.key == this.deletedGenreKey);
            },
        },
        mounted() {
            getGenreService().getGenres().then(genres => {
                this.genres = genres;
                this.contentLoaded = true;
            });
        },
        methods: {
            /**
             * @param {string} genreKey
             */
            handleGenreDelete(genreKey) {
                this.deletedGenreKey = genreKey;
            },

            async handleConfirmGenreDelete() {
                if (await getGenreService().deleteGenre(this.deletedGenreKey)) {
                    this.genres = this.genres.filter(g => g.key != this.deletedGenreKey);
                    this.deletedGenreKey = null;
                }
            },
        },
    };
</script>

<style lang="scss" scoped>
    .content {
        box-sizing: content-box;
        max-width: 600px;
        margin: 0 auto;
        padding: 0 40px;

        .header {
            h1 {
                color: #666;
                font-size: 1.5em;
            }

            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
</style>
