<template>
    <div v-if="genre" class="content" :class="contentClass">
        <header>
            <h1>{{ title }}</h1>
        </header>
        <section class="input-fields">
            <DefaultInputField placeholder="Nazwa" :value="genre.name" @input="handleNameChange" />
        </section>
        <section class="book-selection">
            <GenreBookList
                :genre="genre"
                :books="books"
                @change="$emit('change', $event)"
                @bookDelete="$emit('bookDelete', $event)"
                @reorder="$emit('reorder', $event)"
                />
            <SmallButton
                class="add-book"
                border-color="primary"
                @click.native="addBooksPopup=true">Dodaj</SmallButton>
            <AddBooksPopup
                :enabled="addBooksPopup"
                :genre="genre"
                @addBook="handleAddBook"
                @close="addBooksPopup=false"
                />
        </section>
        <section class="actions">
            <SmallRouterLink to="/tab2">Anuluj</SmallRouterLink>
            <SmallButton border-color="primary" @click.native="handleSaveClicked">Zapisz</SmallButton>
        </section>
    </div>
</template>

<script>
    import DefaultInputField from '@/components/input/DefaultInputField.vue';
    import SmallRouterLink from '@/components/input/SmallRouterLink.vue';
    import SmallButton from '@/components/input/SmallButton.vue';
    import BookSearch from '@/components/BookSearch.vue';
    import GenreBookList from '@/components/genreView/GenreBookList.vue';
    import AddBooksPopup from '@/components/genreView/AddBooksPopup.vue';
    import { getBookService } from '../../services/bookService';

    export default {
        name: 'GenreForms',
        components: {
            DefaultInputField,
            SmallRouterLink,
            SmallButton,
            BookSearch,
            GenreBookList,
            AddBooksPopup,
        },

        props: {
            saving: {
                type: Boolean,
                default: false,
            },
            genre: {
                required: true,
            },
            books: {
                required: true,
            },
            title: {
                type: String,
                required: true,
            }
        },

        data() {
            return {
                addBooksPopup: false,
            };
        },

        computed: {
            contentClass() {
                return {
                    'saving': this.saving,
                };
            },

            unaddedBooks() {
                const rows = Object.keys(this.books)
                    .sort()
                    .filter(key => !this.genre.books.includes(key))
                    .map(key => this.books[key]);
                return rows;
            }
        },

        methods: {
            handleSaveClicked() {
                this.$emit('save', this.genre);
            },

            handleNameChange(name) {
                this.$emit('change', { name });
            },

            handleAddBook(bookKey) {
                this.$emit('change', {
                    books: [
                        bookKey,
                        ...this.genre.books,
                    ],
                });
            },
        },
    };
</script>

<style lang="scss" scoped>
    @import "@/assets/scss/_variables.scss";

    .content {
        display: grid;
        box-sizing: content-box;
        max-width: 900px;
        margin: 0 auto;
        margin-bottom: 200px;
        padding: 0 60px;

        grid-template-areas:
            'header actions'
            'inputs inputs'
            'books books';
        grid-template-columns: 1fr auto;
        grid-gap: 50px 50px;

        &.saving {
            opacity: 0.5;
            pointer-events: none;
        }
    }

    header {
        grid-area: header;
        h1 {
            font-size: 1.5em;
            color: $dark-text;
        }
    }

    .input-fields {
        grid-area: inputs;
        flex: 1 1;
    }

    .book-selection {
        grid-area: books;

        .add-book {
            display: flex;
            flex-direction: column;
            align-items: stretch;
            float: right;
            margin: 20px 0;
        }
    }

    .actions {
        grid-area: actions;
        display: flex;
        justify-content: flex-end;
        align-items: center;

        & > *:not(:last-child) {
            margin-right: 10px;
        }
    }

    // Mobile style
    @media (max-width: $phone-max-width) {
        .content {
            grid-template-areas:
                'header'
                'inputs'
                'books'
                'actions';
            grid-template-columns: 1fr;
            grid-gap: 10px 0px;
        }
    }
</style>
