<template>
    <div>
        <MainPageHeader/>
        <PageNotFound v-if="loadError" :message="loadError" />
        <BookForms pageTitle="Edytuj książkę" :book="book" :saving="saving" @change="handleBookChange" @save="handleSave" />
        <ContentLoader v-if="!book || saving" />
    </div>
</template>

<script>
    import MainPageHeader from '../components/MainPageHeader.vue';
    import { getBookService } from '../services/bookService';
    import PageNotFound from '../components/PageNotFound.vue';
    import BookForms from '../components/bookView/BookForms.vue';
    import DefaultInputField from '../components/input/DefaultInputField.vue';
    import TextArea from '../components/input/TextArea.vue';
    import SmallRouterLink from '../components/input/SmallRouterLink.vue';
    import SmallButton from '../components/input/SmallButton.vue';
    import ImageUpload from '../components/input/ImageUpload.vue';
    import TabletPreview from '../components/bookList/TabletPreview.vue';
    import ContentLoader from '../components/ContentLoader.vue';

    export default {
        name: 'EditBook',
        components: {
            MainPageHeader,
            PageNotFound,
            BookForms,
            ContentLoader,
        },

        data() {
            return {
                book: null,
                loadError: null,
                saving: false,
            };
        },

        methods: {
            handleBookChange(updatedFields) {
                this.book = {
                    ...this.book,
                    ...updatedFields,
                };
            },

            async handleSave(book) {
                this.saving = true;

                const result = await getBookService().updateBook(book);

                if (result) {
                    this.$router.push({ name: 'tab1' });
                } else {
                    console.error(`Something went wrong during the saving.`);
                }

                this.saving = false;
            },
        },

        mounted() {
            (async () => {
                this.book = await getBookService().getBook(this.$route.params.id);

                if (this.book === null) {
                    this.loadError = `The book with the specified id doesn't exist`;
                }
            })();
        },
    };
</script>

