<template>
    <div class="GenreRow">
        <p class="genre-name">{{ genre.name }}</p>
        <SmallRouterLink :to="routerLink" icon="edit" border-color="white">
            Edytuj
        </SmallRouterLink>
        <SmallButton
            icon="minus"
            border-color="white"
            @click.native="$emit('delete', genre.key)"
            >
            Usuń
        </SmallButton>
    </div>
</template>

<script>
    import SmallRouterLink from '@/components/input/SmallRouterLink.vue';
    import SmallButton from '@/components/input/SmallButton.vue';

    export default {
        name: 'GenreRow',
        components: { SmallRouterLink, SmallButton },
        props: {
            genre: {
                required: true,
            },
        },
        computed: {
            routerLink() {
                return `/tab2/edit-genre/${this.genre.key}`;
            }
        },
    };
</script>

<style lang="scss" scoped>
    @import '@/assets/scss/_variables.scss';

    .GenreRow {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .genre-name {
            flex: 1 1;
            margin-right: 20px;
            color: $dark-text;
        }

        & > *:not(:last-child) {
            margin-right: 10px;
        }
    }
</style>
