<template>
    <div>
        <MainPageHeader/>
        <div class="content">
            <div class="header">
                <h1>tekst1_tab1</h1>
                <SmallRouterLink to="/tab1/add-book" icon="plus" border-color="primary">Dodaj</SmallRouterLink>
            </div>
            <BookSearch :value="query" @input="handleSearchInput" @search="handleSearch" />
            <div class="book-list" :class="bookListClass">
                <BookRow v-for="book in books" :key="book.key" :book="book" />
            </div>
        </div>
        <ContentLoader v-if="!contentLoaded" />
    </div>
</template>

<script>
    import BookRow from '../components/bookList/BookRow.vue';
    import BookSearch from '../components/BookSearch.vue';
    import MainPageHeader from '../components/MainPageHeader.vue';
    import SmallRouterLink from '../components/input/SmallRouterLink.vue';
    import InputField from '../components/input/InputField.vue';
    import ContentLoader from '../components/ContentLoader.vue';
    import { getBookService } from '../services/bookService';
    import { createCancellable } from '../helpers/asyncHelper';

    export default {
        name: 'Tab1',
        components: {
            MainPageHeader,
            InputField,
            SmallRouterLink,
            BookRow,
            BookSearch,
            ContentLoader,
        },
        data() {
            return {
                books: [],
                contentLoaded: false,
                query: '',
                searching: false,
            };
        },
        computed: {
            bookListClass() {
                return {
                    'searching': this.searching,
                };
            },
        },
        methods: {
            handleSearchInput(query) {
                this.query = query;
            },

            handleSearch(query) {
                this.searching = true;

                if (this.bookFetchAction) {
                    this.bookFetchAction.cancel();
                }

                this.bookFetchAction = createCancellable(() => 
                    getBookService().getBooks(query)
                );

                this.bookFetchAction().then(books => {
                    console.log('Updated books');
                    this.books = books;
                    this.searching = false;
                    this.bookFetchAction = null;
                });
            },
        },
        mounted() {
            getBookService().getBooks(this.query).then(books => {
                this.books = books;
                this.contentLoaded = true;
            });
        },
    };
</script>

<style lang="scss" scoped>
    .content {
        box-sizing: content-box;
        max-width: 600px;
        margin: 0 auto;
        padding: 0 40px;

        .header {
            h1 {
                color: #666;
                font-size: 1.5em;
            }

            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        .book-list {
            transition: 0.1s ease-out opacity;

            &.searching {
                opacity: 0.5;
            }
        }
    }
</style>
