<template>
    <div class="BookRow">
        <p class="book-name">{{ book.name }}</p>
        <SmallRouterLink :to="routerLink" icon="edit" border-color="white">
            Edytuj
        </SmallRouterLink>
    </div>
</template>

<script>
    import SmallRouterLink from '@/components/input/SmallRouterLink.vue';

    export default {
        name: 'BookRow',
        components: { SmallRouterLink },
        props: {
            book: {
                required: true,
            },
        },
        computed: {
            routerLink() {
                return `/tab1/edit-book/${this.book.key}`;
            }
        },
    };
</script>

