<template>
    <div v-if="book" class="content" :class="contentClass">
        <header>
            <h1>{{ pageTitle }}</h1>
        </header>
        <section class="input-fields">
            <DefaultInputField placeholder="Tytuł" :value="book.name" @input="handleNameChange" />
            <TextArea placeholder="Opis" :value="book.description" @input="handleDescriptionChange" />
            <ImageList
                class="image-list"
                :images="imageList"
                @moveUp="handleImageMoveUp"
                @moveDown="handleImageMoveDown"
                @delete="handleImageDelete"
                />
            <ImageUpload placeholder="Add photo" @input="handleImageUploaded" />
        </section>
        <section class="tablet-preview">
            <TabletPreview :book="book" :imageList="imageList" />
        </section>
        <section class="actions">
            <SmallRouterLink to="/tab1">Anuluj</SmallRouterLink>
            <SmallButton border-color="primary" @click.native="handleSaveClicked" >Zapisz</SmallButton>
        </section>
    </div>
</template>

<script>
    import DefaultInputField from '@/components/input/DefaultInputField.vue';
    import TextArea from '@/components/input/TextArea.vue';
    import SmallRouterLink from '@/components/input/SmallRouterLink.vue';
    import SmallButton from '@/components/input/SmallButton.vue';
    import ImageList from '@/components/input/ImageList.vue';
    import ImageUpload from '@/components/input/ImageUpload.vue';
    import TabletPreview from '@/components/bookList/TabletPreview.vue';
    import { uniqueId } from 'lodash';

    export default {
        name: 'BookForms',
        components: {
            DefaultInputField,
            TextArea,
            SmallRouterLink,
            SmallButton,
            ImageList,
            ImageUpload,
            TabletPreview,
        },

        props: {
            pageTitle: {
                type: String,
                required: true,
            },
            saving: {
                type: Boolean,
                default: false,
            },
            book: {
                required: true,
            },
        },

        data() {
            return {
                imageList: null,
            };
        },

        mounted() {
            this.$watch('book', () => {
                if (!this.book) {
                    return;
                }
                
                this.initImageList();
            });
        },

        computed: {
            contentClass() {
                return {
                    'saving': this.saving,
                };
            },
        },

        methods: {
            initImageList() {
                if (this.imageList != null) {
                    return;
                }

                this.imageList = this.book.images.map(image => ({
                    key: uniqueId('image-'),
                    image,
                }));
            },

            offsetImage(imageKey, offset) {
                const element = this.imageList.find(i => i.key == imageKey);
                const oldIndex = this.imageList.indexOf(element);
                const newIndex = Math.min(Math.max(0, oldIndex + offset), this.imageList.length - 1);

                const newImageList = this.imageList.slice();
                newImageList.splice(oldIndex, 1);
                newImageList.splice(newIndex, 0, element);
                this.imageList = newImageList;
            },

            handleNameChange(name) {
                this.$emit('change', { name });
            },

            handleDescriptionChange(description) {
                this.$emit('change', { description });
            },

            handleImageUploaded(image) {
                this.initImageList();

                this.imageList.push({
                    key: uniqueId('image-'),
                    image,
                });
            },

            handleSaveClicked() {
                this.$emit('save', {
                    ...this.book,
                    images: this.imageList.map(row => row.image),
                });
            },

            handleImageMoveUp(imageKey) {
                this.offsetImage(imageKey, -1);
            },

            handleImageMoveDown(imageKey) {
                this.offsetImage(imageKey, 1);
            },

            handleImageDelete(imageKey) {
                this.imageList = this.imageList.filter(row => row.key != imageKey);
            },
        },
    };
</script>

<style lang="scss" scoped>
    @import "@/assets/scss/_variables.scss";

    .content {
        display: grid;
        box-sizing: content-box;
        max-width: 900px;
        margin: 0 auto;
        padding: 0 60px;

        grid-template-areas:
            'header actions'
            'inputs tablet';
        grid-template-columns: 1fr auto;
        grid-gap: 50px 50px;

        &.saving {
            opacity: 0.5;
            pointer-events: none;
        }
    }

    header {
        grid-area: header;
        h1 {
            font-size: 1.5em;
            color: $dark-text;
        }
    }

    .input-fields {
        grid-area: inputs;
        flex: 1 1;

        .image-list {
            margin-top: 10px;
        }
    }

    .tablet-preview {
        grid-area: tablet;
        display: flex;
        justify-content: space-between;
    }

    .actions {
        grid-area: actions;
        display: flex;
        justify-content: flex-end;
        align-items: center;

        & > * {
            margin-right: 10px;
        }
    }

    // Mobile style
    @media (max-width: $phone-max-width) {
        .content {
            grid-template-areas:
                'header'
                'inputs'
                'actions';
            grid-template-columns: 1fr;
            grid-gap: 10px 0px;
        }
    }
</style>
