<template>
    <div class="ImagePreview" :style="imagePreviewStyle">
    </div>
</template>

<script>
import { uniqueId } from 'lodash';

export default {
    name: 'ImagePreview',
    props: {
        imageSrc: {
            type: String,
            required: true,
        },
    },
    computed: {
        imagePreviewStyle() {
            return {
                backgroundImage: this.imageSrc ? `url(${this.imageSrc})` : undefined,
            }
        }
    },
};
</script>

<style lang="scss" scoped>
    .ImagePreview {
        height: 200px;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
    }
</style>
