<template>
    <div>
        <MainPageHeader/>
        <BookForms pageTitle="Dodaj książkę" :book="book" :saving="saving" @change="handleBookChange" @save="handleSave" />
        <ContentLoader v-if="!book || saving" />
    </div>
</template>

<script>
    import MainPageHeader from '../components/MainPageHeader.vue';
    import { getBookService } from '../services/bookService';
    import PageNotFound from '../components/PageNotFound.vue';
    import BookForms from '../components/bookView/BookForms.vue';
    import ContentLoader from '../components/ContentLoader.vue';

    export default {
        name: 'AddBook',
        components: {
            MainPageHeader,
            PageNotFound,
            BookForms,
            ContentLoader,
        },

        data() {
            return {
                book: {
                    key: null,
                    name: '',
                    description: '',
                    images: [],
                },
                saving: false,
            };
        },

        methods: {
            handleBookChange(updatedFields) {
                this.book = {
                    ...this.book,
                    ...updatedFields,
                };
            },

            async handleSave(book) {
                this.saving = true;

                const result = await getBookService().addBook(book);

                if (result) {
                    this.$router.push('/tab1');
                } else {
                    console.error(`Something went wrong during the saving.`);
                }

                this.saving = false;
            },
        },
    };
</script>

