/* eslint-disable import/prefer-default-export */

import { waitForSeconds } from '../helpers/asyncHelper';
import { uniqueId } from 'lodash';
import '../types/book.types';

/**
 * Contains a dictionary of books, each element of type KeylessBook.
 * @type {{[key: string]: KeylessBook}}
 */
export const booksDatabase = {
    [uniqueId('book-')]: {
        name: 'Book1',
        description: 'Eyy, that pretty gud',
        images: [],
    },
    [uniqueId('book-')]: {
        name: 'Book2',
        description: 'Eyy, that pretty gud',
        images: [],
    },
    [uniqueId('book-')]: {
        name: 'Book3',
        description: 'Not that great',
        images: [],
    },
    [uniqueId('book-')]: {
        name: 'Book4',
        description: 'Passable',
        images: [],
    },
};

export const BookServiceMock = {
    /**
     * Fetches books.
     * 
     * @param {string|undefined} filter An optional filter, which specifies
     *                                  a substring the name has to contain.
     * @returns {Promise<Book[]>} A promise for a book array.
     */
    async getBooks(filter) {
        // Fake a server response delay.
        await waitForSeconds(0.1 + Math.random() * 0.1);

        let books = Object.keys(booksDatabase).map(key => ({
            ...booksDatabase[key],
            key,
        }));

        // Applying an optional filter.
        if (filter) {
            books = books.filter(book => book.name.toLowerCase().includes(filter.toLowerCase()));
        }
        return books;
    },

    /**
     * Returns a promise for a book dictionary, where each item is of type Book.
     * @returns {{[key: string]: Book}}
     */
    async getBooksDictionary() {
        const dictionary = {};

        Object.keys(booksDatabase).forEach(key => {
            dictionary[key] = {
                ...booksDatabase[key],
                key,
            };
        });

        return Promise.resolve(dictionary);
    },

    /**
     * Returns a promise for a book of type Book.
     * 
     * @param {string} key 
     * @returns {(Book|null)} - null if book with specified key wasn't found.
     */
    async getBook(key) {
        // Fake a server response delay.
        await waitForSeconds(0.4 + Math.random() * 0.4);

        const book = booksDatabase[key];

        if (book) {
            return {
                ...book,
                key,
            };
        }
        return null;
    },

    /**
     * Updates a book in the database with the provided info.
     * 
     * @param {Book} book Updated book information. 
     * @returns true if successful, false if not.
     */
    async updateBook(updatedBook) {
        // Fake a server response delay.
        await waitForSeconds(1 + Math.random() * 0.4);

        if (booksDatabase[updatedBook.key]) {
            booksDatabase[updatedBook.key] = {
                ...booksDatabase[updatedBook.key],
                ...updatedBook,
            };
            return true;
        }
        return false;
    },

    /**
     * Adds a book to the database with the provided info.
     * 
     * @param {Book} book The book to add. 
     * @returns true if successful, false if not.
     */
    async addBook(newBook) {
        // Fake a server response delay.
        await waitForSeconds(1 + Math.random() * 0.4);

        newBook.key = uniqueId('book-');
        booksDatabase[newBook.key] = newBook;
        
        return true;
    }
};

export default BookServiceMock;
