<template>
    <div class="Popup" :class="containerClass">
        <div class="popup" :style="containerStyle">
            <slot />
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Popup',

        props: {
            enabled: {
                type: Boolean,
                default: true,
            },
            width: {
                type: String,
                default: '800px',
            },
            height: {
                type: String,
                default: 'unset',
            },
            minWidth: {
                type: String,
                default: 'unset',
            },
            minHeight: {
                type: String,
                default: 'unset',
            },
        },

        computed: {
            containerClass() {
                return {
                    enabled: this.enabled,
                };
            },

            containerStyle() {
                return {
                    width: this.width,
                    height: this.height,
                    minWidth: this.minWidth,
                    minHeight: this.minHeight,
                };
            }
        },

        methods: {
            handleSearchInput(query) {
                this.query = query;
            },

            handleSearch(query) {
                this.searching = true;

                if (this.bookFetchAction) {
                    this.bookFetchAction.cancel();
                }

                this.bookFetchAction = createCancellable(() => 
                    getBookService().getBooks(query)
                );

                this.bookFetchAction().then(books => {
                    this.books = filterUnaddedBooks(this.genre, books);;
                    this.searching = false;
                    this.bookFetchAction = null;
                });
            },

            handleAddBook(bookKey) {
                this.$emit('addBook', bookKey);
                this.books = this.books.filter(book => book.key != bookKey);
            }
        },
    };
</script>

<style lang="scss" scoped>
    .Popup {
        &:not(.enabled) {
            pointer-events: none;
            background-color: rgb(0, 0, 0, 0);

            .popup {
                display: none;
            }
        }

        pointer-events: unset;
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 66px;
        background-color: rgb(0, 0, 0, 0.7);
        transition: 0.1s ease-out background-color;

        .popup {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            max-width: calc(100% - 40px);
            max-height: calc(100vh - 160px);
            padding: 20px;

            background-color: black;
            border: 1px solid white;
        }
    }
</style>