<template>
    <div class="TabletPreview">
        <div class="tablet" :style="tabletStyle">
            <div class="tablet-content">
                <pre class="description">{{ book.description }}</pre>
                <div class="image-preview-list">
                    <ImagePreview
                        v-for="row in imageList"
                        :key="row.key"
                        :imageSrc="row.image"
                        :style="{ height: 'unset' }"
                        />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import ipadMock from '../../assets/images/ipadmock.png';
    import ImagePreview from '../ImagePreview.vue';

    export default {
        name: 'TabletPreview',
        components: {
            ImagePreview,
        },
        props: {
            book: {
                required: true,
            },
            imageList: {
                required: true,
            },
        },
        computed: {
            tabletStyle() {
                return {
                    backgroundImage: `url(${ipadMock})`,
                };
            },
        },
    };
</script>

<style lang="scss" scoped>
    @import '@/assets/scss/_variables.scss';

    .TabletPreview {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .tablet {
            position: relative;
            min-width: 400px;
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;

            &:after {
                content: '';
                display: block;
                padding-top: 145%;
            }

            .tablet-content {
                overflow-x: hidden;
                overflow-y: auto;
                position: absolute;
                left: 53px;
                right: 50px;
                top: 89px;
                bottom: 94px;

                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                padding: 20px;
                background-color: #eeeeee;
                color: black;

                .description {
                    font-family: 'Roboto', sans-serif;
                    margin: 0;
                    margin-bottom: 50px;
                    word-wrap: break-word;
                    white-space: pre-wrap;
                }

                .image-previews {
                    flex: 1 1;
                    min-height: 200px;
                    background-size: contain;
                    background-repeat: no-repeat;
                    background-position: center top;
                }
            }
        }

        .book-name {
            margin-right: 20px;
            color: $dark-text;
        }
    }

    // Mobile style
    @media (max-width: $phone-max-width) {
        .TabletPreview {
            display: none;
        }
    }
</style>
