/* eslint-disable import/prefer-default-export */

import { waitForSeconds } from '../helpers/asyncHelper';
import { uniqueId } from 'lodash';
import { booksDatabase } from './bookServiceMock';
import '../types/genre.types';

/**
 * Contains a dictionary of books, each element of type @type {Genre}
 * 
 */
const genreDatabase = {
    [uniqueId('genre-')]: {
        name: 'Thriller',
        books: Object.keys(booksDatabase),
    },
    [uniqueId('genre-')]: {
        name: 'Romance',
        books: Object.keys(booksDatabase).slice(2),
    },
    [uniqueId('genre-')]: {
        name: 'Fantasy',
        books: Object.keys(booksDatabase).slice(0, 2),
    },
};

export const GenreServiceMock = {
    /**
     * Returns a promise for a genre array, where each item is of type Genre.
     */
    async getGenres() {
        // Fake a server response delay.
        await waitForSeconds(0.1 + Math.random() * 0.1);

        let genres = Object.keys(genreDatabase).map(key => ({
            ...genreDatabase[key],
            key,
        }));

        return genres;
    },

    /**
     * Returns a promise for a genre of type Genre.
     * 
     * @param {string} key 
     * @returns null if genre with specified key wasn't found.
     */
    async getGenre(key) {
        // Fake a server response delay.
        await waitForSeconds(0.4 + Math.random() * 0.4);

        const genre = genreDatabase[key];

        if (genre) {
            return {
                ...genre,
                key,
            };
        }
        return null;
    },

    /**
     * Updates a genre in the database with the provided info.
     * 
     * @param {Genre} genre Updated genre information. 
     * @returns true if successful, false if not.
     */
    async updateGenre(updatedGenre) {
        // Fake a server response delay.
        await waitForSeconds(1 + Math.random() * 0.4);

        if (genreDatabase[updatedGenre.key]) {
            genreDatabase[updatedGenre.key] = {
                ...genreDatabase[updatedGenre.key],
                ...updatedGenre,
            };
            return true;
        }
        return false;
    },

    /**
     * Adds a genre to the database with the provided info.
     * 
     * @param {Genre} newGenre The genre to add. 
     * @returns true if successful, false if not.
     */
    async addGenre(newGenre) {
        // Fake a server response delay.
        await waitForSeconds(1 + Math.random() * 0.4);

        newGenre.key = uniqueId('genre-');
        genreDatabase[newGenre.key] = newGenre;
        
        return true;
    },

    async deleteGenre(genreKey) {
        // Fake a server response delay.
        await waitForSeconds(Math.random() * 0.4);

        if (genreDatabase[genreKey]) {
            genreDatabase[genreKey] = undefined;
            delete genreDatabase[genreKey];
            return true;
        }

        return false;
    }
};

export default GenreServiceMock;
