<template>
<div class="input-field-wrapper">
    <div class="input-field-group">
        <div class="text-input">
            <input
                    :id="id"
                    :type="type"
                    :placeholder="placeholder"
                    :value="value"
                    autocomplete="off"
                    @keyup="inputChanged"
                    required
            />
            <label :for="id">{{placeholder}}</label>
        </div>
    </div>
</div>
</template>

<script>
import { uniqueId } from 'lodash';

export default {
    name: 'DefaultInputField',
    props: {
        placeholder: {
            type: String,
            default: '',
        },
        type: {
            type: String,
            default: 'text',
        },
        value: {
            type: String,
            required: true,
        },
    },
    data() {
        return {
            id: null,
        };
    },
    mounted() {
        this.id = uniqueId('input-field-group-');
    },
    methods: {
        inputChanged(event) {
            this.$emit('input', event.target.value);
        },
    },
};
</script>

