        margin-bottom: 16px;
    }

    .text-input > input {
        position: absolute;
        bottom: 0;
        display: block;
        height: 40px;
        width: 100%;
        line-height: 40px;
        border: 0;
        border-bottom: 1px solid $grey-light;
        border-radius: 0;
        outline: 0;
        background: transparent;
        font-size: 14px;
        color: $grey-light;
        /*   transition: all 1s ease-in-out; */
    }

    .text-input > input::placeholder {
        color: transparent;
    }

    .text-input > label {
        position: absolute;
        top: 25px;
