<template>
    <div class="ImageUpload">
        <div class="input-field">
            <input
                    :id="id"
                    type="file"
                    autocomplete="off"
                    @change="inputChanged"
            />
            <label :for="id">{{ buttonText }}</label>
        </div>
    </div>
</template>

<script>
import { uniqueId } from 'lodash';

export default {
    name: 'ImageUpload',
    props: {
        placeholder: {
            type: String,
            required: true,
        },
    },
    data() {
        return {
            id: null,
            value: null,
        };
    },
    mounted() {
        this.id = uniqueId('input-field-group-');
    },
    computed: {
        buttonText() {
            return this.placeholder;
        },

    },
    methods: {
        inputChanged(event) {
            const preview = new Image();
            const file = document.getElementById(this.id).files[0];
            const reader = new FileReader();

            reader.addEventListener('load', () => {
                // Convert image file to base64 string
                this.$emit('input', reader.result);
            }, false);

            if (file) {
                reader.readAsDataURL(file);
            }
        },
    },
};
</script>

<style lang="scss" scoped>
    @import "@/assets/scss/_variables.scss";
    
    .ImageUpload {
        width: 100%;
        margin: 10px 0;

        .preview {
            height: 200px;
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;
        }

        .input-field {
            margin-top: 16px;
            display: flex;
            
            label {
                width: 100%;
                padding: 16px 0;
                cursor: pointer;
                border: 1px dashed $grey-light;
                font-size: 16px;
                color: $grey-light;
                text-align: center;
            }

            input {
                display: none;
            }
        }
    }
</style>
