<template>
    <Popup class="ConfirmDeleteGenrePopup" :enabled="enabled" width="500px" minHeight="200px">
        <h1>Czy na pewno chcesz usunąć gatunek <strong>"{{ genreName }}"</strong>?</h1>
        <div class="actions">
            <SmallButton @click.native="$emit('confirm')" border-color="primary">Usuń</SmallButton>
            <SmallButton @click.native="$emit('close')">Cofnij</SmallButton>
        </div>
    </Popup>
</template>

<script>
    import Popup from '@/components/generic/Popup.vue';
    import SmallButton from '@/components/input/SmallButton.vue';
    import '../../types/genre.types';

    export default {
        name: 'ConfirmDeleteGenrePopup',
        components: {
            Popup,
            SmallButton,
        },

        props: {
            enabled: {
                type: Boolean,
                default: true,
            },
            genre: {
                required: true,
            }
        },

        computed: {
            genreName() {
                return this.genre ? this.genre.name : '';
            },
        },
    };
</script>

<style lang="scss" scoped>
    .ConfirmDeleteGenrePopup {
        h1 {
            margin: 0;
            margin-bottom: 20px;
            color: white;

            font-weight: normal;
        }

        .actions {
            display: flex;
            justify-content: flex-end;

            & > *:not(:last-child) {
                margin-right: 10px;
            }
        }
    }
</style>