<template>
    <div class="BookSearch">
        <DefaultInputField
            type="text"
            :placeholder="placeholder"
            :value="value"
            @input="inputChanged"
        />
        <font-awesome-icon icon="search" />
    </div>
</template>

<script>
import DefaultInputField from '@/components/input/DefaultInputField.vue';

const DEBOUNCE_TIMEOUT = 0;

export default {
    name: 'BookSearch',
    components: { DefaultInputField },
    props: {
        value: {
            type: String,
            required: true,
        },
        placeholder: {
            type: String,
            default: 'Wyszukaj',
        },
    },
    data() {
        return {
            debounceTimeout: null,
        };
    },
    methods: {
        inputChanged(event) {
            this.$emit('input', event);

            if (this.debounceTimeout) {
                clearTimeout(this.debounceTimeout);
            }

            this.debounceTimeout = setTimeout(() => this.$emit('search', event), DEBOUNCE_TIMEOUT);
        },
    }
};
</script>

<style lang="scss" scoped>
    @import "@/assets/scss/_variables.scss";
    @import "@/assets/scss/bulma/projectBulma.scss";

    .BookSearch {
        position: relative;

        svg {
            position: absolute;
            right: 0;
            bottom: 28px;
            color: white;

            transition: 0.1s ease-out color;
        }

        &:focus-within {
            svg {
                color: $primary;
            }
        }
    }
</style>
