<template>
    <div class="GenreBookRowContainer" :class="containerClass">
        <div class="GenreBookRow" :class="rowClass" :style="rowStyle">
            <div class="drag-handle" @mousedown="onMouseDown" @touchstart="onTouchStart">
                <font-awesome-icon icon="bars" />
            </div>
            <p class="order-number">{{ order+1 }}</p>
            <p class="book-name">{{ book.name }}</p>
            <SmallButton icon="minus" border-color="white" @click.native="handleDelete">
                Usuń
            </SmallButton>
        </div>
    </div>
</template>

<script>
    import SmallButton from '@/components/input/SmallButton.vue';

    export default {
        name: 'GenreBookRow',
        components: { SmallButton },
        props: {
            order: {
                type: Number,
                required: true,
            },
            book: {
                required: true,
            },
            dragged: {
                type: Boolean,
                default: false,                
            },
            dragHighlight: {
                type: Boolean,
                default: false,
            },
        },
        data() {
            return {
                anchorX: 0,
                anchorY: 0,
                dragX: 0,
                dragY: 0,
                touchMode: false,
                touchIdentifier: null,
            };
        },
        computed: {
            containerClass() {
                return {
                    dragged: this.dragged,
                };
            },

            rowClass() {
                return {
                    dragged: this.dragged,
                };
            },

            rowStyle() {
                if (this.dragged) {
                    return {
                        left: `${Math.floor(this.dragX - this.anchorX)}px`,
                        top: `${Math.floor(this.dragY - this.anchorY)}px`,
                    };
                }
                return {};
            }
        },
        methods: {
            // Touch
            onTouchStart(e) {
                const rect = e.target.getBoundingClientRect();

                const touches = e.changedTouches;
                if (touches.length > 0) {
                    const touch = touches[0];
                    this.dragX = touch.clientX;
                    this.dragY = touch.clientY;
                    this.anchorX = touch.clientX - rect.left;
                    this.anchorY = touch.clientY - rect.top;

                    this.touchIdentifier = touch.identifier;
                    this.touchMode = true;

                    this.$emit('dragstart', {
                        key: this.book.key,
                        x: e.clientX,
                        y: e.clientY,
                    });
                }
            },

            onTouchMove(e) {
                const touches = e.changedTouches;
                for (const touch of touches) {
                    if (touch.identifier == this.touchIdentifier) {
                        this.dragX = touch.clientX;
                        this.dragY = touch.clientY;
                        
                        this.$emit('dragmove', {
                            key: this.book.key,
                            x: touch.clientX,
                            y: touch.clientY,
                        });
                        return;
                    }
                }
            },

            onTouchEnd(e) {
                const touches = e.changedTouches;
                for (const touch of touches) {
                    if (touch.identifier == this.touchIdentifier) {
                        this.dragX = touch.clientX;
                        this.dragY = touch.clientY;
                        
                        this.$emit('dragend', {
                            key: this.book.key,
                            x: touch.clientX,
                            y: touch.clientY,
                        });
                        return;
                    }
                }
            },

            // Mouse

            onMouseDown(e) {
                if (this.touchMode)
                    return;

                const rect = e.target.getBoundingClientRect();
                this.anchorX = e.clientX - rect.left;
                this.anchorY = e.clientY - rect.top;
                
                this.$emit('dragstart', {
                    key: this.book.key,
                    x: e.clientX,
                    y: e.clientY,
                });
            },

            onMouseMove(e) {
                if (this.touchMode)
                    return;

                this.dragX = e.clientX;
                this.dragY = e.clientY;

                this.$emit('dragmove', {
                    key: this.book.key,
                    x: e.clientX,
                    y: e.clientY,
                });
            },

            onMouseUp(e) {
                if (this.touchMode)
                    return;
                
                this.$emit('dragend', {
                    key: this.book.key,
                    x: e.clientX,
                    y: e.clientY,
                });
            },

            handleDelete() {
                this.$emit('delete', this.book.key);
            }
        },

        mounted() {
            const touchDetector = () => {
                this.touchMode = true;
                document.removeEventListener('touchstart', touchDetector);
            };
            document.addEventListener('touchstart', touchDetector);

            this.listeners = [
                [ 'touchmove', document.addEventListener('touchmove', e => this.onTouchMove(e)) ],
                [ 'touchend', document.addEventListener('touchend', e => this.onTouchEnd(e)) ],
                [ 'mousemove', document.addEventListener('mousemove', e => this.onMouseMove(e)) ],
                [ 'mouseup', document.addEventListener('mouseup', e => this.onMouseUp(e)) ],
            ];
        },

        beforeDestroy() {
            for (const [ event, listener ] of this.listeners) {
                document.removeEventListener(event, listener);
            }
        }
    };
</script>

<style lang="scss" scoped>
    @import '@/assets/scss/_variables.scss';

    @keyframes shake {
        $steps: 10;
        @for $i from 0 to $steps {
            $x: #{random(3) - 2}px;
            $y: #{random(3) - 2}px;

            #{$i / $steps * 100%} { transform: translate($x, $y) }
        }
    }

    .GenreBookRowContainer {
        height: 51px;
    }

    .GenreBookRow {
        touch-action: none;
        user-select: none;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-left: -40px;

        transition: 0.2s ease-out width;

        .book-name {
            flex: 1 1;
            margin-right: 20px;
            color: $dark-text;
        }

        .order-number {
            color: $dark-text;
            margin-right: 10px;
        }

        .drag-handle {
            align-self: stretch;

            display: flex;
            align-items: center;
            box-sizing: content-box;
            padding: 10px;
            padding-right: 20px;
            flex: 0 0 auto;
            color: $dark-text;
            cursor: pointer;
            transition: 0.05s ease-out color;

            &:hover {
                color: #aaa;
            }

            svg {
                pointer-events: none;
            }
        }

        &.dragged {
            margin-left: 0px;
            position: fixed;
            width: 950px;
            max-width: calc(100vw - 80px);
            background-color: #111;

            .drag-handle {
                background-color: #111;
                color: white;
            }

            .book-name {
                color: white;
            }
        }

        &.tapped {
            background-color: #111;

            .drag-handle {
                background-color: #111;
                color: white;
            }

            .book-name {
                color: white;
            }
        }

        &.shake {
            .drag-handle {
                animation-name: shake;
                animation-duration: 0.6s;
                animation-iteration-count: infinite;
                animation-timing-function: linear;
            }
        }
    }
</style>
