<template>
    <div>
        <MainPageHeader/>
        <PageNotFound v-if="loadError" :message="loadError" />
        <GenreForms
            title="Edytuj gatunek"
            :genre="genre"
            :books="books"
            :saving="saving"
            @change="handleGenreChange"
            @reorder="handleReorder"
            @bookDelete="handleBookDelete"
            @save="handleSave"
            />
        <ContentLoader v-if="!genre || saving" />
    </div>
</template>

<script>
    import { getBookService } from '../services/bookService';
    import { getGenreService } from '../services/genreService';
    import MainPageHeader from '../components/MainPageHeader.vue';
    import PageNotFound from '../components/PageNotFound.vue';
    import BookSearch from '@/components/BookSearch.vue';
    import ContentLoader from '../components/ContentLoader.vue';
    import GenreForms from '@/components/genreView/GenreForms.vue';
    import '../types/genre.types';

    export default {
        name: 'EditGenre',
        components: {
            MainPageHeader,
            PageNotFound,
            BookSearch,
            ContentLoader,
            GenreForms,
        },

        data() {
            return {
                genre: null,
                books: null,
                loadError: null,
                saving: false,
            };
        },

        async mounted() {
            const [ genre, books ] = await Promise.all([
                getGenreService().getGenre(this.$route.params.id),
                getBookService().getBooksDictionary(),
            ]);
            this.genre = genre;
            this.books = books;

            if (this.genre === null) {
                this.loadError = `The genre with the specified id doesn't exist`;
            }
        },

        methods: {
            /**
             * @param {Partial<Genre>} updatedFields
             */
            handleGenreChange(updatedFields) {
                this.genre = {
                    ...this.genre,
                    ...updatedFields,
                };
            },

            async handleSave() {
                this.saving = true;

                const result = await getGenreService().updateGenre(this.genre);

                if (result) {
                    this.$router.push({ name: 'tab2' });
                } else {
                    console.error(`Something went wrong during the saving.`);
                }

                this.saving = false;
            },

            handleReorder({ key, order }) {
                const newBooks = this.genre.books.filter(k => k != key);
                newBooks.splice(Math.max(0, Math.min(order, newBooks.length)), 0, key);
                this.genre.books = newBooks;
            },

            handleBookDelete(bookKey) {
                this.genre.books = this.genre.books.filter(k => k != bookKey);
            },
        },
    };
</script>