<template>
    <div class="GenreBookList" :class="computedClass">
        <BookSearch :value="query" @input="handleSearchInput" />
        <transition-group tag="div" name="book-list" class="book-list" ref="bookList">
            <GenreBookRow
                v-for="row in filteredRows"
                :key="row.key"
                :book="books[row.key]"
                :order="row.order"
                :dragged="draggedKey == row.key"
                :dragHighlight="draggedKey != row.key && tapDragging"
                @dragstart="handleDragStarted"
                @dragmove="handleDragMove"
                @dragend="handleDragFinished"
                @dragtap="handleDragTap"
                @delete="handleBookDelete"
                />
        </transition-group>
    </div>
</template>

<script>
    import DefaultInputField from '@/components/input/DefaultInputField.vue';
    import SmallRouterLink from '@/components/input/SmallRouterLink.vue';
    import SmallButton from '@/components/input/SmallButton.vue';
    import BookSearch from '@/components/BookSearch.vue';
    import GenreBookRow from '@/components/genreView/GenreBookRow.vue';
    import { getBookService } from '../../services/bookService';

    export default {
        name: 'GenreBookList',
        components: {
            DefaultInputField,
            SmallRouterLink,
            SmallButton,
            BookSearch,
            GenreBookRow,
        },

        props: {
            genre: {
                required: true,
            },
            books: {
                required: true,
            },
        },

        data() {
            return {
                query: '',
                draggedKey: null,
                draggedOrder: null,
                tapDragging: false,
                rowHeight: 51,
            };
        },

        computed: {
            computedClass() {
                return {
                    'tap-dragging': this.tapDragging,
                };
            },

            filteredRows() {
                const rows = this.genre.books.map((key, order) => ({
                    key, order,
                }));

                if (this.query) {
                    return rows.filter(row => this.books[row.key].name.toLowerCase().includes(this.query.toLowerCase()));
                }

                return rows;
            }
        },

        methods: {
            handleBookDelete(key) {
                this.$emit('bookDelete', key);
            },

            handleSaveClicked() {
                this.$emit('save', this.genre);
            },

            handleSearchInput(query) {
                this.query = query;
            },

            handleDragStarted({ key, x, y }) {
                this.query = '';
                this.draggedKey = key;
                this.draggedOrder = Object.keys(this.genre.books).find(order => this.genre.books[order] == key);
            },

            handleDragFinished() {
                this.draggedKey = null;
            },

            handleDragMove({ key, x, y }) {
                if (this.draggedKey && this.$refs.bookList) {
                    const rect = this.$refs.bookList.$el.getBoundingClientRect();
                    const order = Math.floor((y - rect.top) / this.rowHeight);
                    
                    if (order != this.draggedOrder) {
                        this.$emit('reorder', {
                            key: this.draggedKey,
                            order,
                        });
                        this.draggedOrder = order;
                    }
                }
            },

            handleDragTap({ key, x, y }) {
                this.query = '';
                if (this.draggedKey) {
                    // Finding the order of the tapped row.
                    const tappedOrder = Object.keys(this.genre.books).find(order => this.genre.books[order] == key);

                    // Finding the order of the source row.
                    const srcOrder = Object.keys(this.genre.books).find(order => this.genre.books[order] == this.draggedKey);

                    console.log(`${srcOrder} -> ${tappedOrder}`);

                    // Reordering the dragged row.
                    this.$emit('reorder', {
                        key: this.draggedKey,
                        order: tappedOrder,
                    });

                    // Reordering the tapped row.
                    this.$emit('reorder', {
                        key,
                        order: srcOrder,
                    });
                    
                    this.draggedKey = null;
                    this.tapDragging = false;
                } else {
                    this.draggedKey = key;
                    this.tapDragging = true;
                }
            },
        },
    };
</script>

<style lang="scss" scoped>
    @import "@/assets/scss/_variables.scss";

    .GenreBookList {
        grid-area: books;
    }

    // Transitions
    .book-list-move:not(.dragged) {
        transition: transform ease-out 0.2s;
    }
</style>
