<template>
    <Popup class="AddBooksPopup" :enabled="enabled" maxWidth="800px" height="500px">
        <h1>Przypisz książki do gatunku</h1>
        <BookSearch 
            :value="query"
            @input="handleSearchInput"
            @search="handleSearch" />
        <div class="book-list" :class="bookListClass">
            <GenreAddBookRow
                v-for="book in books"
                :key="book.key"
                :book="book"
                @add="handleAddBook"
                />
            <div class="list-message">{{ listMessage }}</div>
        </div>
        <SmallButton @click.native="$emit('close')">Zamknij</SmallButton>
    </Popup>
</template>

<script>
    import Popup from '@/components/generic/Popup.vue';
    import BookSearch from '@/components/BookSearch.vue';
    import GenreAddBookRow from '@/components/genreView/GenreAddBookRow.vue';
    import SmallButton from '@/components/input/SmallButton.vue';
    import { createCancellable } from '../../helpers/asyncHelper';
    import { getBookService } from '../../services/bookService';
    import '../../types/genre.types';
    import '../../types/book.types';

    /**
     * @param {Genre} genre
     * @param {Book[]} books
     */
    function filterUnaddedBooks(genre, books) {
        return books.filter(book => !genre.books.includes(book.key));
    }

    export default {
        name: 'AddBooksPopup',
        components: {
            Popup,
            BookSearch,
            GenreAddBookRow,
            SmallButton,
        },

        props: {
            enabled: {
                type: Boolean,
                default: true,
            },
            genre: {
                required: true,
            }
        },

        data() {
            return {
                books: [],
                contentLoaded: false,
                query: '',
                searching: false,
            };
        },

        computed: {
            bookListClass() {
                return {
                    'searching': this.searching,
                };
            },

            listMessage() {
                return this.books.length > 0 ? '' : 'No books to show';
            }
        },

        methods: {
            handleSearchInput(query) {
                this.query = query;
            },

            handleSearch(query) {
                this.searching = true;

                if (this.bookFetchAction) {
                    this.bookFetchAction.cancel();
                }

                this.bookFetchAction = createCancellable(() => 
                    getBookService().getBooks(query)
                );

                this.bookFetchAction().then(books => {
                    this.books = filterUnaddedBooks(this.genre, books);;
                    this.searching = false;
                    this.bookFetchAction = null;
                });
            },

            handleAddBook(bookKey) {
                this.$emit('addBook', bookKey);
                this.books = this.books.filter(book => book.key != bookKey);
            }
        },

        mounted() {
            getBookService().getBooks(this.query).then(books => {
                this.books = filterUnaddedBooks(this.genre, books);
            });

            this.$watch('enabled', () => {
                if (this.enabled) {
                    this.query = '';
                    this.handleSearch(this.query);
                }
            });
        },
    };
</script>

<style lang="scss" scoped>
    .AddBooksPopup {
        h1 {
            margin: 0;
            margin-bottom: 20px;
            color: white;
        }

        .book-list {
            flex: 1 1;
            position: relative;
            transition: 0.1s ease-out opacity;
            overflow-y: auto;

            &.searching {
                opacity: 0.5;
            }

            .list-message {
                pointer-events: none;
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                color: #555555;
            }
        }
    }
</style>
