<template>
    <router-link class="SmallRouterLink" :class="computedClass" :to="to">
        <p class="label">
            <slot></slot>
        </p>
        <font-awesome-icon v-if="icon" :icon="icon" />
    </router-link>
</template>

<script>
export default {
    name: 'SmallRouterLink',
    props: {
        to: {
            type: String,
            required: true,
        },
        borderColor: {
            type: String,
            default: 'white',
        },
        icon: {
            type: String,
            default: null,
        },
    },
    computed: {
        computedClass() {
            return {
                [`border-${this.borderColor}`]: true,
                hasIcon: this.icon != null,
            };
        },
    },
    methods: {
        click() {
            this.$emit('click');
        },
    },
};
</script>

<style lang="scss" scoped>
    @import '@/assets/scss/_variables.scss';
    @import "@/assets/scss/bulma/projectBulma.scss";

    .SmallRouterLink {
        padding: 5px;
        min-width: 100px;
        background-color: $black;
        border: solid 1px $primary;
        color: $dark-text;
        text-align: center;
        font-size: 0.8em;
        font-weight: 400;

        &.border-white {
            border-color: $white;
        }

        &.border-primary {
            border-color: $primary;
        }

        .label {
            padding: 0;
            margin: 0 !important;
            font-size: 1em;
            color: white;
        }

        svg {
            display: none;
            transition: 0.05s ease-out color;
        }
    }

    // Mobile style
    @media (max-width: $phone-max-width) {
        .SmallRouterLink.hasIcon {
            border: none;
            min-width: unset;
            font-size: 1.5em;

            .label {
                display: none;
            }

            svg {
                display: block;
            }

            &:hover {
                color: #aaa;
            }
        }
    }
</style>
